/* eslint-disable react/prop-types */
import React from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import { animated } from 'react-spring/web.cjs';
import TruncatedTooltipText from '@elliemae/ds-basic/TruncatedTooltipText';
import { sortableHandle } from 'react-sortable-hoc';
import GripperVertical from '@elliemae/ds-icons/GripperVertical';
import { isMovable } from '../../helper';

const blockName = 'shuttle-list-item';
const Wrapper = aggregatedClasses(animated.li)(blockName);
const ActionsGroup = aggregatedClasses('div')(blockName, 'actions');
const IconWrapper = aggregatedClasses('div')(blockName, 'icon-wrapper');
const Content = aggregatedClasses('label')(
  blockName,
  'content',
  ({ isChecked, canMove, isReadonly }) => ({
    checked: isChecked,
    'can-hover': canMove,
    'read-only': isReadonly,
  }),
);

const SortHandler = sortableHandle(() => (
  <GripperVertical className="gripper" />
));

function ShuttleListItem({
  item = {},
  showIcons = undefined,
  showIcon = true,
  onSelect = () => null,
  isChecked = false,
  style = undefined,
  showActions = true,
  actions = [],
  showSortHandler = false,
  ...rest
}) {
  return (
    <Wrapper {...rest} role="listitem" style={style}>
      <Content
        classProps={{
          canMove: isMovable(item),
          isChecked,
          isReadonly: item.readyOnly,
        }}
        onClick={onSelect}
      >
        {showSortHandler && <SortHandler />}
        {(showIcon || showIcons) && <IconWrapper>{item.icon}</IconWrapper>}
        <TruncatedTooltipText
          className="shuttle-tooltip-text"
          value={item.name}
        />
      </Content>
      {showActions && <ActionsGroup>{actions}</ActionsGroup>}
    </Wrapper>
  );
}

export default ShuttleListItem;
