import React, { useCallback, useContext } from 'react';
import ShuttleSource from './components/ShuttleSource';
import ShuttleTarget from './components/ShuttleTarget';
import { ShuttleContainer } from './classedComponents';
import AnimationContext from './AnimationState';

// todo: clean methods on jsx
const noop = () => {};
function ShuttleRenderer({
  containerProps,
  onNavigateOnTargetTo = noop,
  onNavigateOnSourceTo = noop,
  onMoveItemToSource = noop,
  onMoveItemToTarget = noop,
  onClearMovedItems = noop,
  onMoveCheckedItems = noop,
  onClearCheckedItems = noop,
  onToggleSearch = noop,
  onSearch = noop,
  onSearchClose = noop,
  onSearchOpen = noop,
  searching = false,
  onCheckItem = noop,
  hierarchy = [],
  hierarchyDest = [],
  checkedItems = [],
  sourceItems = [],
  targetItems = [],
  sourceEmptyMessage,
  targetEmptyMessage,
  composeTargetItemProps,
  composeSourceItemProps,
  targetSortable,
  sourceSortable,
  onSourceSortEnd,
  onTargetSortEnd,
  loadingSource = false,
  loadingTarget = false,
  onGetMoreItems,
  moreItemsLoading,
  hasNextPage,
  ...otherProps
}) {
  const { setIsMoving, setIsDrillingDown, setIsMovingBack, reset } = useContext(
    AnimationContext.Context,
  );

  const removeAnimationState = useCallback(() =>
    setTimeout(() => reset(), 500),
  );

  return (
    <ShuttleContainer {...containerProps}>
      <ShuttleSource
        {...otherProps}
        checkedItems={checkedItems}
        composeSourceItemProps={composeSourceItemProps}
        emptyMessage={sourceEmptyMessage}
        hierarchy={hierarchy}
        items={sourceItems}
        onCheckItem={onCheckItem}
        onClearCheckedItems={onClearCheckedItems}
        onMoveCheckedItems={item => {
          setIsMoving(true);
          onMoveCheckedItems(item);
        }}
        onMoveItem={item => {
          setIsMoving(true);
          onMoveItemToTarget(item);
          removeAnimationState();
        }}
        onNavigateTo={item => {
          const indexInHierarchy = hierarchy.findIndex(
            hItem => hItem.id === item.id,
          );
          setIsDrillingDown(indexInHierarchy === -1);
          onNavigateOnSourceTo(item);
        }}
        onSearch={onSearch}
        onSearchClose={onSearchClose}
        onSearchOpen={onSearchOpen}
        onSortEnd={onSourceSortEnd}
        onToggleSearch={onToggleSearch}
        searching={searching}
        sourceSortable={sourceSortable}
        loading={loadingSource}
        onGetMoreItems={onGetMoreItems}
        moreItemsLoading={moreItemsLoading}
        hasNextPage={hasNextPage}
      />
      <ShuttleTarget
        {...otherProps}
        composeTargetItemProps={composeTargetItemProps}
        emptyMessage={targetEmptyMessage}
        hierarchy={hierarchyDest}
        items={targetItems}
        onClearMovedItems={() => {
          setIsMovingBack(true);
          removeAnimationState();
          onClearMovedItems();
        }}
        onMoveItem={item => {
          setIsMovingBack(true);
          removeAnimationState();
          onMoveItemToSource(item);
        }}
        onNavigateTo={item => {
          const indexInHierarchy = hierarchyDest.findIndex(
            hItem => hItem.id === item.id,
          );
          setIsDrillingDown(indexInHierarchy === -1);
          onNavigateOnTargetTo(item);
        }}
        onSortEnd={onTargetSortEnd}
        targetSortable={targetSortable}
        loading={loadingTarget}
      />
    </ShuttleContainer>
  );
}

export default ShuttleRenderer;
