import DSButton from '@elliemae/ds-basic/Button';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import ShuttleBreadcrumb from './components/ShuttleBreadcrumb';
import OverflowList from './components/OverflowList';

// block names
const blockName = 'shuttle';
const headerBlockName = `${blockName}-header`;
const listBlockName = `${blockName}-list`;
const footerBlockName = `${blockName}-footer`;

export const ShuttleContainer = aggregatedClasses('div')(blockName);

// wrapper
export const ShuttleWrapper = aggregatedClasses('div')(`${blockName}-wrapper`);

// header
export const ShuttleHeader = aggregatedClasses('div')(headerBlockName);
export const ShuttleHeaderBreadcrumb = aggregatedClasses(ShuttleBreadcrumb)(
  headerBlockName,
  'breadcrumb',
);
export const ShuttleHeaderSearchToggle = aggregatedClasses(DSButton)(
  headerBlockName,
  'search-toggle',
);

// list
export const ShuttleList = aggregatedClasses('div')(
  listBlockName,
  null,
  ({ showPulse }) => ({
    'show-pulse': showPulse,
  }),
);

export const ShuttleListPanel = aggregatedClasses('div')(
  listBlockName,
  'panel',
  ({ open }) => ({ open }),
);
export const Overflow = aggregatedClasses(OverflowList)(
  listBlockName,
  'overflow',
  ({ empty, searching }) => ({
    empty,
    searching,
  }),
);
export const EmptyMessage = aggregatedClasses('span')(
  listBlockName,
  'empty-message',
);
export const LoadingList = aggregatedClasses('div')(
  listBlockName,
  'loading-list',
);

// footer
export const ShuttleFooter = aggregatedClasses('div')(footerBlockName);
export const ShuttleFooterActions = aggregatedClasses('div')(
  footerBlockName,
  'actions',
);
export const ShuttleFooterCounter = aggregatedClasses('span')(
  footerBlockName,
  'counter',
);
