/* eslint-disable react/prop-types */
import React, { useContext, useEffect, useRef } from 'react';
import { useTransition } from 'react-spring/web.cjs';
import { FixedSizeList as List } from 'react-window';
import InfiniteLoader from 'react-window-infinite-loader';
import AutoSizer from 'react-virtualized-auto-sizer';
import { animationConfig } from '../animation/animationConfig';
import AnimationState from '../AnimationState';
import VirtualizedItem from './VirtualizedItem';

function VirtualizedList({
  items,
  itemRenderer,
  searching,
  target = false,
  innerRef,
  children,
  hasNextPage = true,
  getMoreItems = () => {},
}) {
  const hasMounted = useRef(false);
  const {
    state: { isMoving, isMovingBack },
    reset,
  } = useContext(AnimationState.Context);

  useEffect(() => {
    hasMounted.current = true;
  }, []);

  const transitions = useTransition(items, (item) => item.id, {
    ...animationConfig.moveItem(target),
    onDestroyed: () => reset(),
  });

  const itemCount = hasNextPage ? items.length + 1 : items.length;

  const isItemLoaded = (index) => !hasNextPage || index < items.length;

  return (
    <AutoSizer>
      {({ height, width }) => (
        <InfiniteLoader
          isItemLoaded={isItemLoaded}
          itemCount={itemCount}
          loadMoreItems={getMoreItems}
        >
          {({ onItemsRendered, ref }) => (
            <List
              height={searching ? 300 : height}
              width={width}
              innerRef={innerRef}
              itemCount={transitions.length}
              itemData={{
                items: transitions,
                itemRenderer,
                isItemMoving: hasMounted.current && (isMoving || isMovingBack),
              }}
              itemSize={35}
              onItemsRendered={onItemsRendered}
              ref={ref}
            >
              {children || VirtualizedItem}
            </List>
          )}
        </InfiniteLoader>
      )}
    </AutoSizer>
  );
}

export default VirtualizedList;
