import React from 'react';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import DSButton from '@elliemae/ds-basic/Button';
import CloseCircle from '@elliemae/ds-icons/CloseCircle';
import ArrowShortRight from '@elliemae/ds-icons/ArrowShortRight';
import ArrowShortReturn from '@elliemae/ds-icons/ArrowShortReturn';

const ActionButton = aggregatedClasses(DSButton)(
  'shuttle-list-item',
  'action-button',
);

export const DeleteButton = props => (
  <ActionButton
    buttonType="text"
    icon={<CloseCircle />}
    {...props}
    // containerProps={{ 'data-testid': 'ds-shuttle__close-button' }}
  />
);

export const MoveButton = props => (
  <ActionButton
    buttonType="text"
    icon={<ArrowShortRight />}
    {...props}
    containerProps={{ 'data-testid': 'ds-shuttle__move-button' }}
  />
);

export const DrillDownButton = props => (
  <ActionButton
    buttonType="text"
    icon={<ArrowShortReturn data-testid="shuttle__drill-btn-icon" />}
    {...props}
    containerProps={{ 'data-testid': 'shuttle__drill-btn' }}
  />
);
