import React, { useContext, useEffect, useRef } from 'react';
import DSButton from '@elliemae/ds-basic/Button';
import { isFunction, orderBy } from '@elliemae/ds-utilities/utils';
import { useForceUpdate } from '@elliemae/ds-utilities/hooks';
import {
  EmptyMessage,
  Overflow,
  ShuttleFooter,
  ShuttleFooterActions,
  ShuttleFooterCounter,
  ShuttleHeader,
  ShuttleHeaderBreadcrumb,
  ShuttleList,
  ShuttleWrapper,
  LoadingList,
} from '../classedComponents';
import { getActiveIdFromHierarchy, moveItems } from '../helper';
import AnimationContext from '../AnimationState';
import VirtualizedList from './VirtualizedList';
import ShuttleTargetListItem from './ShuttleListItem/ShuttleTargetListItem';
import VirtualizedSortableList from './VirtualizedSortableList';
import LoadingIndicator from './LoadingIndicator';

const noop = () => {};

function ShuttleTarget({
  items = [],
  hierarchy = [],
  onNavigateTo = noop,
  onMoveItem = noop,
  onClearMovedItems = noop,
  emptyMessage = 'No items selected',
  composeTargetItemProps = () => ({}),
  targetSortable = false,
  onSortEnd = noop,
  targetClearItemsText = 'CLEAR ALL',
  renderTargetCounter = amount => `${amount} items`,
  loading = false,
}) {
  const {
    state: { isMoving = false },
  } = useContext(AnimationContext.Context);
  const isEmpty = !items.length;
  const emptyContent = loading ? (
    <LoadingIndicator />
  ) : (
    <EmptyMessage>{emptyMessage}</EmptyMessage>
  );

  const ComponentList = targetSortable
    ? VirtualizedSortableList
    : VirtualizedList;

  const handleSortEnd = ({ oldIndex, newIndex }) => {
    onSortEnd({
      sourceItem: items[oldIndex],
      targetItem: items[newIndex],
    });
  };

  return (
    <ShuttleWrapper>
      <ShuttleHeader>
        <ShuttleHeaderBreadcrumb
          hierarchy={hierarchy}
          onNavigateTo={onNavigateTo}
        />
      </ShuttleHeader>
      <ShuttleList classProps={{ showPulse: isMoving }}>
        {!loading && !isEmpty ? (
          <Overflow
            activeHierarchy={getActiveIdFromHierarchy(hierarchy).id}
            classProps={{
              empty: !items.length,
            }}
          >
            <ComponentList
              itemRenderer={({ item, ...rest }) => (
                <ShuttleTargetListItem
                  data-testid="target-list-item"
                  {...rest}
                  {...composeTargetItemProps(item, rest)}
                  isRoot={!getActiveIdFromHierarchy(hierarchy).id}
                  item={item}
                  onMoveToSource={onMoveItem}
                  onNavigateOnTargetTo={onNavigateTo}
                  showSortHandler={targetSortable}
                />
              )}
              items={items}
              onSortEnd={handleSortEnd}
              target
            />
          </Overflow>
        ) : (
          <LoadingList>{emptyContent}</LoadingList>
        )}
      </ShuttleList>
      <ShuttleFooter>
        <ShuttleFooterActions>
          {!!items.length && (
            <DSButton
              buttonType="link"
              labelText={targetClearItemsText}
              onClick={onClearMovedItems}
            />
          )}
        </ShuttleFooterActions>
        <ShuttleFooterCounter>
          {renderTargetCounter(items.length)}
        </ShuttleFooterCounter>
      </ShuttleFooter>
    </ShuttleWrapper>
  );
}

export default ShuttleTarget;
