import { useCallback, useState } from 'react';
import createContainer from 'constate';

function useShuttleAnimationState() {
  const [animationState, setAnimationState] = useState({
    isMoving: false,
    isMovingBack: false,
    isDrillingDown: false,
  });
  const reset = useCallback(
    () =>
      setAnimationState({
        isMoving: false,
        isMovingBack: false,
        isDrillingDown: false,
      }),
    [],
  );
  const move = useCallback(
    () =>
      setAnimationState({
        isMovingBack: false,
        isMoving: true,
        isDrillingDown: false,
      }),
    [],
  );

  const moveBack = useCallback(
    () =>
      setAnimationState({
        isMovingBack: true,
        isMoving: false,
        isDrillingDown: false,
      }),
    [],
  );

  const drilldown = useCallback(
    () =>
      setAnimationState({
        isMovingBack: false,
        isMoving: false,
        isDrillingDown: true,
      }),
    [],
  );

  return {
    state: animationState,
    setIsMoving: move,
    setIsMovingBack: moveBack,
    setIsDrillingDown: drilldown,
    reset,
  };
}

export default createContainer(useShuttleAnimationState, value => [
  value.state,
]);
