import { compose, isEqual, hashArray } from '@elliemae/ds-utilities/utils';
import { getItemsById, pipe, prepareItems } from './helper';
import { addToTarget, resetMovedItems } from './Shuttle.actions';

const mergeToPrevState = (prevState, nextState) => ({
  ...prevState,
  ...nextState,
  prevProps: {
    ...prevState.prevProps,
    ...nextState.prevProps,
  },
});

function compareSelectedItems(nextProps, prevProps) {
  const { selectedItems, idField } = nextProps;
  return nextState => {
    if (isEqual(selectedItems, prevProps.selectedItems)) return nextState;

    const { preparedItems } = nextState;
    const prevSelectedItemsHash = hashArray(prevProps.selectedItems || []);

    const itemsToMove = getItemsById(selectedItems, preparedItems);
    const actions = itemsToMove.map(item =>
      prevSelectedItemsHash[item[idField]]
        ? addToTarget(item, nextState.targetItems)
        : addToTarget(item),
    );

    return mergeToPrevState(nextState, {
      ...pipe([resetMovedItems(), ...actions])(nextState, nextProps),
      prevProps: {
        ...nextState.prevProps,
        selectedItems,
      },
    });
  };
}

function compareItems(nextProps, prevProps) {
  const { items } = nextProps;
  return nextState => {
    if (isEqual(items, prevProps.items)) return nextState;
    return mergeToPrevState(nextState, {
      preparedItems: prepareItems(items),
      prevProps: {
        items,
      },
    });
  };
}

export default function(nextProps, prevState) {
  const stateReducers = [compareSelectedItems, compareItems].map(fn =>
    fn(nextProps, prevState.prevProps),
  );

  const nextState = compose(...stateReducers)(prevState);
  return !isEqual(nextState, prevState) ? nextState : null;
}
