import React from 'react';
import PropTypes from 'prop-types';
import { aggregatedClasses } from '@elliemae/ds-classnames';

const blockName = 'breadcrumb';

function DSBreadcrumbItem({
  containerComponent = 'li',
  as: LinkComponent = 'a',
  active = false,
  href = undefined,
  isTitle,
  onClick = () => null,
  label = '',
  ...rest
}) {
  const CrumbWrapper = aggregatedClasses(containerComponent)(
    blockName,
    'crumb',
    ({ active: a, title }) => ({
      active: a,
      title,
    }),
  );
  const Link = aggregatedClasses(LinkComponent)(blockName, 'label');
  return (
    <CrumbWrapper
      aria-current={active ? 'page' : undefined}
      data-testid="breadcrumb-item-container"
      classProps={{ active, title: isTitle }}
      {...rest}
    >
      <Link href={href} onClick={onClick}>
        {label}
      </Link>
    </CrumbWrapper>
  );
}

DSBreadcrumbItem.propTypes = {
  containerComponent: PropTypes.string,
  as: PropTypes.string,
  active: PropTypes.bool,
  href: PropTypes.string,
  onClick: PropTypes.func,
  isTitle: PropTypes.bool,
  trailingSlash: PropTypes.bool,
  label: PropTypes.string,
};

export default DSBreadcrumbItem;
