/* eslint-disable react/prop-types */
import React from 'react';
import { useTransition, animated } from 'react-spring/web.cjs';
import DSBreadcrumb from '@elliemae/ds-basic/Breadcrumb';
import { animationConfig } from '../animation/animationConfig';

function ShuttleBreadcrumb({
  className = '',
  hierarchy = [],
  onNavigateTo = () => null,
}) {
  const animatedCrumbs = useTransition(
    hierarchy,
    (item) => item.id,
    animationConfig.breadcrumb(),
  );

  return (
    <div className={className}>
      <DSBreadcrumb isTitle>
        {animatedCrumbs.map(({ item, props, key }) => (
          <DSBreadcrumb.Item
            key={key}
            containerComponent={animated.li}
            label={item.name}
            onClick={() => onNavigateTo(item)}
            style={props}
          />
        ))}
      </DSBreadcrumb>
    </div>
  );
}

export default ShuttleBreadcrumb;
