import {
  addOrRemove,
  cloneDeep,
  hashArray,
  compose,
} from '@elliemae/ds-utilities/utils';
import {
  getHierarchyTypeName,
  toggleInObject,
  filterMovedItems,
} from './helper';

import utils from './utils';

export function toggleItemSelection(itemId) {
  return state => ({
    ...state,
    checkedItems: addOrRemove(state.checkedItems, itemId),
  });
}
export function navigateTo(item, dest) {
  const hierarchyType = getHierarchyTypeName(dest);
  return state => {
    const indexInHierarchy = state[hierarchyType].findIndex(
      hItem => hItem.id === item.id,
    );
    const hierarchy = state[hierarchyType];
    return {
      ...state,
      [hierarchyType]:
        indexInHierarchy !== -1
          ? hierarchy.slice(0, indexInHierarchy + 1)
          : [...hierarchy, item],
    };
  };
}
export function moveItem(item) {
  return state => {
    const movedItems = toggleInObject(state.movedItems, item.id, item);
    return {
      ...state,
      movedItems,
    };
  };
}

const defaultOptions = {
  parentField: 'parent',
  idField: 'id',
  op: hasMoved => !hasMoved,
};

// todo: optimize this
function getItemDeep({ collection, id, options }) {
  const { idField } = { ...defaultOptions, ...options };
  const itemIndex = collection.findIndex(fItem => fItem[idField] === id);

  // get item with nested children
  const item = collection[itemIndex];
  const children = item?.hasChildren
    ? compose(utils.getChildren({ index: itemIndex }))(collection) || []
    : [];

  return { item, children };
}

export function addToTarget(itemToAdd, collection) {
  return (state, { idField }) => {
    const items = collection || state.preparedItems;

    const { item, children } = getItemDeep({
      collection: items,
      id: itemToAdd[idField],
    });

    return {
      ...state,
      movedItems: [...state.movedItems, item],
      targetItems: [...state.targetItems, item, ...children],
    };
  };
}

// todo: optimize this
export function removeFromTarget(itemToRemove) {
  return (state, { idField }) => {
    let targetItems = cloneDeep(state.targetItems);

    const itemIndex = targetItems.findIndex(
      fItem => fItem.id === itemToRemove.id,
    );

    const item = targetItems[itemIndex];
    const children = item?.hasChildren
      ? compose(
          filterMovedItems(state.movedItems),
          utils.getChildren({ index: itemIndex }),
        )(targetItems) || []
      : [];

    targetItems.splice(itemIndex, 1);
    if (item?.hasChildren && children.length) {
      const childrenHash = hashArray(children, 'id');
      targetItems = targetItems.filter(i => !childrenHash[i.id]);
    }

    return {
      ...state,
      movedItems: state.movedItems.filter(
        mItem => mItem[idField] !== itemToRemove[idField],
      ),
      targetItems,
    };
  };
}

export function resetCheckedItems() {
  return state => ({
    ...state,
    checkedItems: [],
  });
}
export function resetMovedItems() {
  return state => ({
    ...state,
    movedItems: [],
    targetItems: [],
  });
}
